  background: -webkit-linear-gradient(top, rgba(59, 89, 153, 1), rgba(59, 89, 153, 1));
  border-radius: 20px;
}
#main .loginfb::after {
  content: 'Login with FaceBook';
  color: #dfe0e6;
  font-weight: 400;
  font-size: 13px;
  display: block;
  margin-top: 10px;
  padding-top: 7px;
  text-align: center;
}
